:root {
	--padding: calc(var(--safe-area-inset-bottom) + var(--tabbar_height));
	--bottom: calc(var(--tabbar_height) + var(--safe-area-inset-bottom));
}
.Tabbar {
	animation: tabbarAnimation 0.7s ease;
}
.Panel__in {
	padding-bottom: var(--padding)!important;
}
.Gallery__bullets {
	bottom: calc(var(--safe-area-inset-bottom) + 10px);
}
.Div, .Header {
	-webkit-user-select: none;
	user-select: none;
}
.Epic .FixedLayout--bottom {
	padding-bottom: var(--bottom)!important;
}
.Alert__btn {
	cursor: pointer;
}
.Tabbar--l-horizontal .TabbarItem__text {
	animation: textAnimation 0.2s ease-out;
}
.Avatar__img, .Banner--ios .Banner__in {
	border-radius: 35px!important;
}
.Avatar__shadow {
	display: none;
}
.Banner__header {
	margin-top: 7px!important;
	font-size: 1.6em!important;
}
.Banner__subheader {
	margin-top: 10px!important;
	font-size: 1.17em!important;
	line-height: normal!important;
	color: #000000!important;
}
.Banner__in {
	margin-bottom: 35px!important;
}
#Apple { color: #ffffff; }
#Spotify { color: #000000; }
#Yandex { color: #000000; }

.SongList {
	animation: tabbarAnimation 0.7s ease;
}
@keyframes textAnimation {
  0% {
    opacity: 0.1;
    transform:  translate(0px, 50px)  ;
  }
  100% {
    opacity: 1;
    transform:  translate(0px, 0px)  ;
  }
}
@keyframes tabbarAnimation {
	0% {
	  transform:  translate(0px, 100px)  ;
	}
	100% {
	  transform:  translate(0px, 0px)  ;
	}
  }
@keyframes tabbarAnimation {
	0% {
	  transform:  translate(0px, -100px)  ;
	}
	85% {
		transform:  translate(0px, 10px)  ;
	}
	100% {
	  transform:  translate(0px, 0px)  ;
	}
  }
@media screen and (max-width: 333px) {
	.IntroImg {
		width: 250px!important;
		height: 250px!important;
	}
	.Button--sz-l .Button__content {
		font-size: 13px;
	}
}
@media screen and (max-width: 313px) {
	.Button--sz-l .Button__content {
		font-size: 11px;
	}
}
@media screen and (max-width: 298px) {
	.Button--sz-l .Button__content {
		font-size: 9px;
	}
}
@media screen and (max-width: 283px) {
	.Button--sz-l .Button__content {
		font-size: 8.2px;
	}
}